import { writable } from "svelte/store";
import { Settings } from "./storage";
function createSettingStore(name) {
    const store = writable(Settings[name]);
    store.subscribe(value => {
        Settings[name] = value;
    });
    return store;
}
export const isLightThemeStore = createSettingStore("isLightTheme");
export const authAppStateStore = writable("authAppState");
